let timer = '';
import http from '@/api/http.js'
import QSPopup from '@/components/QS-popup/QS-popup.vue';
export default{
	components:{QSPopup},
	data(){
		return{
			bannerImg:[],
			windowHeight:"667px",
			autoplay:true,
			timers:5,
			nav:false,
			page:1
		}
	},
	onLoad(option) {
		this.getGuide()
		// uni.setStorageSync('looktype',0)

		let scene = decodeURIComponent(option.scene)//参数二维码传递过来的参数
		// let query = options.query.dentistId // 参数二维码传递过来的场景参数
		let q = decodeURIComponent(option.q)
		console.log(q)
		console.log(scene)
		// console.log(query)

		console.log(uni.getStorageSync('looktype'))
		
		console.log(uni.getStorageSync('looktype'))
		
		
















		
	},
	onReady() {

		let _me = this;
		uni.getSystemInfo({//获取手机屏幕高度信息，让swiper的高度和手机屏幕一样高
			success: function(res) {
				console.log(res.model);
				console.log(res.pixelRatio);
				console.log(res.windowWidth);
				console.log(res.windowHeight);//这里是手机屏幕高度
				console.log(res.language);
				console.log(res.version);
				console.log(res.platform);
				_me.windowHeight = res.windowHeight + 'px';
				console.log('手机屏幕高度为' + _me.windowHeight);
			}
		});

	},
	onShow() {
		
		if(!uni.getStorageSync('looktype')){
			this.autoplay = false
			setTimeout(()=>{
				this.show('QSPopup')
			},50)
		}else{
			// this.autoplay = true
			// this.countdown()
			if(this.page == this.bannerImg.length){
				this.nav = true
				this.autoplay = false
			}else{
				this.nav = false
				this.autoplay = true
			}
			this.countdown()
		}
		
	},
	methods:{
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		surehide(){
			this.hides('QSPopup')
			uni.setStorageSync('looktype',1)
			this.autoplay = true
			if(this.bannerImg.length == 1){
				this.nav = true
			}
			this.countdown()
		},
		toPro(){
			uni.navigateTo({
				url:"/pages/index/logon/userpro"
			})
		},
		toAgree(){
			uni.navigateTo({
				url:"/pages/index/logon/useragree"
			})
		},
		getGuide(){
			http('/api/index/guide_list','post').then(res=>{
				console.log(res)
				this.bannerImg = res.data.data
				if(uni.getStorageSync('looktype')){
					if(this.bannerImg.length == 1){
						this.nav = true
						this.countdown()
					}
				}
			})
		},
		bannerTo(item,index) {  //0:无；1外链；2素材；3商品详情；4分类
			this.page = index + 1
			if(item.banner_type==0){
				return
			}else if(item.banner_type==1){
				clearInterval(timer);
				this.autoplay = false
				this.nav = false
				this.countdown()
				uni.navigateTo({
					url: '/pages/index/totalList?src=' + item.link
				})
			}else if(item.banner_type==2){
				clearInterval(timer);
				this.autoplay = false
				this.nav = false
				this.countdown()
				// uni.navigateTo({
				// 	url:"/pages/viper/libDetail?id=" + item.link
				// })
				uni.navigateTo({
					url:"/pages/viper/libDetail?id=" + item.link
				})
			}else if(item.banner_type==3){
				clearInterval(timer);
				this.autoplay = false
				this.nav = false
				this.countdown()
				uni.navigateTo({
					url:"/pages/index/detail?goodsid=" + item.link
				})
			}else if(item.banner_type==4){
				clearInterval(timer);
				this.autoplay = false
				this.nav = false
				this.countdown()
				this.$util.setStorage('indexNum',Number(item.link))
				uni.switchTab({
					url:"/pages/item/item"
				})
			}
		},
		animationfinish(e){
			console.log(e)
			if(e.detail.current == this.bannerImg.length-1){
				this.autoplay = false
				if(this.timers <= 0){
					this.nav = true
				}
				this.countdown()
				//  setTimeout(function() {
				// 	uni.switchTab({
				// 		url: '/pages/index/index'
				// 	});
				// }, 5000)
			}
		},
		changecur(e){
			console.log(e)
			
			if(e.detail.current == this.bannerImg.length-1){
				this.autoplay = false
				this.nav = true
			}else {
				this.autoplay = true
				this.nav = false
			}
			this.countdown()
			console.log(this.nav )
		},
		toIndex(){
			clearInterval(timer);
			this.autoplay = false
			this.nav = false
			this.countdown()
			uni.switchTab({
				url: '/pages/index/index'
			});
		},
		countdown(){
			clearInterval(timer);
			this.timers = 5
			if(this.nav == false){
				timer = setInterval(() => {
				    this.timers--;
				    if (this.timers <= 0) {
				        clearInterval(timer);
				    }
				}, 1000)
			}else{
				timer = setInterval(() => {
				    this.timers--;
				    if (this.timers <= 0) {
						uni.switchTab({
							url: '/pages/index/index'
						});
				        clearInterval(timer);
				    }
				}, 1000)
			}
			
		}
	}
}

